nav {
  background: rgba(0, 0, 0, 0.3);
  width: -webkit-max-content;
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  bottom: 2rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: flex;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  border-radius: 3rem;
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.1rem;
}

nav a:hover {
  background: var(--color-primary);
  color: var(--color-bg);
}

nav a.active {
  background: var(--color-primary);
  color: var(--color-bg);
}

.about_container {
  display: grid;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  gap: 15%;
}

.about_me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.about_me-image {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  overflow: hidden;
  object-fit: cover;
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
  transition: var(--transition);
}

.about_me-image:hover {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}

.about_cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.about_card {
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}

.about_card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  /* cursor: default; */
}

.about_icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.about_card h5 {
  font-size: 0.95rem;
}

.about_card small {
  font: 0.7rem;
  color: var(--color-light);
}

.about_content p {
  white-space: pre-line;
  margin: 2rem 0 2.6rem;
  color: var(--color-light);
}
.about_content p::after {
  content: "\a";
  white-space: pre;
}
.about_content h3 {
  color: var(--color-white);
}

/* ____ MEDIA QUERIES (MEDIUM DEVICES) ________ */
@media screen and (max-width: 1024px) {
  .about_container {
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;
  }
  .about_me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
  .about_content p {
    margin: 1rem 0 1.5rem;
  }
}

/* ____ MEDIA QUERIES (SMALL DEVICES) ________ */
@media screen and (max-width: 600px) {
  .about_me {
    width: 65%;
    margin: 0 auto 3rem;
  }
  .about_cards {
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }
  .about_content {
    text-align: center;
  }
  .about_content p {
    margin: 1.5rem 0;
  }
}

.experience_container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
}

.experience_container > div {
  background: var(--color-bg-variant);
  padding: 2.4rem 5rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.experience_container > div:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  /* cursor: default; */
}

.experience_container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-primary);
}

.experience_content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.experience_details {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.experience_details-icon {
  margin-top: 6px;
  color: var(--color-primary);
}

@media screen and (max-width: 1024px) {
  .experience_container {
    grid-template-columns: 1fr;
  }
  .experience_container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }
  .experience_content {
    padding: 1rem;
  }
}

/* ____ MEDIA QUERIES (SMALL DEVICES) ________ */
@media screen and (max-width: 600px) {
  .experience_container {
    grid-gap: 1rem;
    gap: 1rem;
  }
  .experience_container > div {
    width: 100%;
    padding: 2rem 1rem;
    margin: 0 auto;
  }
  .experience_content {
    padding: 1rem;
  }
}

.portfolio_container {
  display: grid;
  grid-template-columns: 1fr;
  padding-bottom: 3rem;
  max-height: 40rem;
  width: 55% !important;
  border-radius: 1rem;
}

.portfolio_item {
  display: flex;
  flex-direction: column;
  background: var(--color-bg-variant);
  padding: 1.3rem;
  border-radius: 1.2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

/* .portfolio_item:hover {
  border-color: var(--color-primary-variant);
  background: transparent;
} */

.portfolio_item-image {
  border-radius: 1rem;
  overflow: hidden;
  width: 100%;
  height: 60%;
}
.portfolio_item h3 {
  margin: 1.2rem 0;
  text-align: center;
}

.portfolio_item-cta {
  display: flex;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
  /* margin-bottom: 1rem; */
}

/* ____ MEDIA QUERIES (MEDIUM DEVICES) ________ */
@media screen and (max-width: 1024px) {
  .portfolio_container {
    grid-template-columns: 1fr;
    width: 70% !important;
  }
  .portfolio_item {
    padding-bottom: 0;
  }
}

/* ____ MEDIA QUERIES (SMALL DEVICES) ________ */
@media screen and (max-width: 600px) {
  .portfolio_container {
    width: 90% !important;
  }
  .portfolio_item {
    padding-bottom: 0;
    max-height: 90%;
  }
  .spc{
    font-size: 13px;
  }
  .spc a{
    padding: 1rem;
  }

  .portfolio_item-image {
    margin-top: 1rem;
    height: 50%;
  }
}

.container.contact_container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  grid-gap: 12%;
  gap: 12%;
}

.contact_options {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}

.contact_option {
  background: var(--color-bg-variant);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  transition: var(--transition);
}

.contact_option:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
}

.contact_option-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.contact_option a {
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.8rem;
}

.contact_option h5 {
  color: var(--color-light);
}

/*_____________ FORM _____________*/
form {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}

input,
textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: transparent;
  border: 2px solid var(--color-primary-variant);
  resize: none;
  color: var(--color-white);
}

/* ____ MEDIA QUERIES (MEDIUM DEVICES) ________ */
@media screen and (max-width: 1024px) {
  .container.contact_container {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
  }
}

/* ____ MEDIA QUERIES (SMALL DEVICES) ________ */
@media screen and (max-width: 600px) {
  .container.contact_container {
    width: var(--container-width-sm);
  }
}

